#card {
	width: 280px;
	height: 350px;
	overflow: hidden;
	background-color: rgb(138, 182, 97);
	border-radius: 15px;
	font-size: 23px;
	font-family: 'Galada';
	margin: 10px;
}

.card:hover {
	cursor: pointer;
	box-shadow: 0px 0px 40px darkgoldenrod;
}

.card img {
	margin: 5px;
	max-width: 265px;
	max-height: 265px;
	border-radius: 15px;
}

.card span {
	color: white;
}
